import React, { useRef, useState } from 'react';
import logo from './logo.svg';

import { Link, Route, Redirect, RouteProps, RouteChildrenProps } from 'react-router-dom';
import { createUseStyles } from 'react-jss';
import loadingGif from './images/loading.gif';
import PdfViewer from './PdfViewer';
import { FrameworkPage, JsonBlock, getWidthLevelMin, WidthLevelContext } from './utils';

export const debugPages: Record<string, RouteProps["component"]> = {
  '': DebugIndexPage,
  'pdf': PdfDebugPage,
  'default': ReactDefaultPage,
  'test': TestPage,
}

export function createDebugPageRoutes() {
  const routes: React.ReactNode[] = [];

  for(let link in debugPages) {
    const uri = `/debug/${link}`;
    routes.push(<Route exact path={uri} key={uri} component={debugPages[link]} />);
  }
  routes.push(<Route path="/debug/" key="wildcard" render={() => <Redirect to="/debug/" />} />);

  return routes
}

function DebugPageLinks(props: Partial<React.ComponentProps<Link>>) {
  const pages: React.ReactNode[] = [];

  for(let link in debugPages) {
    const uri = `/debug/${link}`;
    pages.push(<li key={uri}><Link {...props} to={uri} children={uri} /></li>);
  }

  return (
    <React.Fragment>
      <h2 style={{marginTop: 0}}>Debug Pages</h2>
      <Link {...props} to="/" children="Back to app index" />
      <ul children={pages} />
    </React.Fragment>
  )
}

function DebugIndexPage() {
  return (
    <FrameworkPage title="Debug Page">
      <DebugPageLinks />
      <p>Width Level: <WidthLevelContext.Consumer children={(v) => v.toString()} /></p>
      <JsonBlock value={process.env} varName="process.env" />
    </FrameworkPage>
  )
}

const usePdfDebugStyles = createUseStyles({
  viewer: {
    maxWidth: '600px',
    margin: [[0, 'auto']],
  },
})

function PdfDebugPage() {
  const classes = usePdfDebugStyles();
  const uriInputRef = useRef<HTMLInputElement>(null);
  const [pdfUri, setPdfUri] = useState<string | undefined>("/data/cv.pdf");
  const disableEditing = (process.env.NODE_ENV !== 'development');
  return (
    <FrameworkPage title="PDF Debug">
      <DebugPageLinks />
      <fieldset>
        <legend>PDF Data</legend>
        {
          disableEditing
          ? undefined
          : <form onSubmit={(e) => {
              e.preventDefault();
              setPdfUri(uriInputRef.current?.value);
            }}>
              <label htmlFor="pdf-uri">URI for PDF file: </label>
              <input type="text" id="pdf-uri" ref={uriInputRef} defaultValue={pdfUri} /><br />
              <input type="submit" />
            </form>
        }
        <a href={pdfUri} target="_blank" rel="noreferrer">Open in new window</a>
      </fieldset>
      Rendered:
      {/* <PdfViewer file={pdfUri} className={classes.viewer} noOverZoom /> */}
    </FrameworkPage>
  )
}



