export function RouterDebugPage(props: RouteChildrenProps) {
  const classes = useDebugPageStyle();
  return (
    <FrameworkPage title={props.location.pathname} className={classes.outerContainer}>
      <div className={classes.page}>
        <p>You are visiting an demo page. Here are your routing information:</p>
        <JsonBlock value={props.location} varName="location" className={classes.infoBlock} />
        <p>For debug information, visit <Link to="/debug/">debug pages</Link>.</p>
      </div>
    </FrameworkPage>
  )
}

