import React, { useState, useEffect, useRef } from 'react';
import { createUseStyles } from 'react-jss';
// import { Document } from 'react-pdf';
import { Document, Page } from 'react-pdf/dist/esm/entry.webpack';
import { PDFDocumentProxy } from 'pdfjs-dist';
import { Debounce } from './utils';

const useStyles = createUseStyles({
  '@keyframes loading-animate': {
    from: {
      backgroundPositionX: 0,
    },
    to: {
      backgroundPositionX: '200%',
    },
  },
  '@keyframes fadeIn': {
    from: {
      opacity: 0,
    },
    to: {
      opacity: 1,
    },
  },
  loading: {
    background: 'linear-gradient(125.75deg, white 0%, #dee4f0 33%, white 66%, #dee4f0 100%)',
    backgroundSize: [['200%', '100%']],
    animation: '$loading-animate 1s infinite linear',
    borderRadius: 8,
    paddingBottom: '141.453%',  // aspect for A4 papers
    color: 'red',
    fontWeight: 'bold',
    padding: 10,
    boxShadow: [[0, 10, 40, 0, 'rgba(16, 36, 94, 0.2)']],
  },
  page: {
    '&>:first-child': { // hack for correct width
      margin: [[0, 'auto']],
      borderRadius: 8,
      boxShadow: [[0, 10, 40, 0, 'rgba(16, 36, 94, 0.2)']],
    },
  },
  pageLoading: {
    background: 'linear-gradient(125.75deg, white 0%, #dee4f0 33%, white 66%, #dee4f0 100%)',
    backgroundSize: [['200%', '100%']],
    animation: '$loading-animate 1s infinite linear, $fadeIn 0.2s 1 ease',  // fadeIn: render almost empty page when page loading costs little time
    paddingBottom: '141.453%',  // aspect for A4 papers
    fontWeight: 'bold',
    padding: 10,
  },
  pageNoData: {
    background: 'linear-gradient(125.75deg, white 0%, #dee4f0 33%, white 66%, #dee4f0 100%)',
    backgroundSize: [['200%', '100%']],
    animation: '$loading-animate 1s infinite linear',
    paddingBottom: '141.453%',  // aspect for A4 papers
    color: 'red',
    fontWeight: 'bold',
    padding: 10,
  },
  document: {
    '&:hover $controls, &:focus-within $controls': {
      opacity: 1,
    },
    position: 'relative', // force positioned
  },
  controls: {
    position: 'absolute', // relative to the nearest positioned ancestor, i.e. <Document>
    bottom: '5%',
    left: '50%',
    background: 'white',
    opacity: 0,
    transform: 'translateX(-50%)',
    transition: 'opacity ease-in-out 0.2s',
    boxShadow: [[0, 10, 40, 0, 'rgba(16, 36, 94, 0.2)']],
    borderRadius: 4,
    '& span': {
      font: 'inherit',
      fontSize: '0.8em',
      padding: [[0, '0.5em']],
    },
    '& button': {
      width: 44,
      height: 44,
      background: 'white',
      border: 0,
      font: 'inherit',
      fontSize: '0.8em',
      borderRadius: 4,
    },
    '& button:enabled:hover': {
      cursor: 'pointer',
      backgroundColor: '#e6e6e6',
    },
    '& button:enabled:focus': {
      // outline: '#767676 dashed 1px',
    },
    '& button:enabled:active': {
      backgroundColor: '#666666',
    },
    '& button:first-child': {
      borderTopRightRadius: 0,
      borderBottomRightRadius: 0,
    },
    '& button:last-child': {
      borderTopLeftRadius: 0,
      borderBottomLeftRadius: 0,
    },
  },
});

export interface PdfViewerProps {
  /**
   * Defines what PDF should be displayed.
   * Its value can be an URL,
   * a file (imported using import ... from ... or from file input form element),
   * or an object with parameters
   *  (
   *   url - URL;
   *   data - data, preferably Uint8Array;
   *   range - PDFDataRangeTransport;
   *   httpHeaders - custom request headers, e.g. for authorization
   *   withCredentials - a boolean to indicate whether or not to include cookies in the request (defaults to false)
   *  )
   */
  file: any

  className?: string
  noOverZoom?: boolean
  options?: Document["props"]['options'];
  externalLinkTarget?: Document["props"]['externalLinkTarget'];
  renderMode?: Document["props"]['renderMode'];
}

function PdfViewer(props: PdfViewerProps) {
  const classes = useStyles();

  const [pageWidth, setPageWidth] = useState<number | undefined>();
  const pageRef = useRef<HTMLDivElement | null>(null);
  useEffect(() => {
    const handleWindowResize = Debounce((event: UIEvent) => {
      setPageWidth(pageRef.current?.clientWidth);
    });
    window.addEventListener("resize", handleWindowResize);
    return () => window.removeEventListener("resize", handleWindowResize);
  }, []);

  const [originalWidth, setOriginalWidth] = useState<number>(Infinity);

  const [pageNum, setPageNum] = useState<number | null>(null);
  const [curPage, setCurPage] = useState<number>(1);
  const [errorMessage, setErrorMessage] = useState("");
  const [loadingProgress, setLoadingProgress] = useState<number>(0);


  const { noOverZoom, className, ...docProps } = props;

  const documentClasses = [classes.document];
  if(className) {
    documentClasses.push(className);
  }
  

  const calculatedWidth =
    noOverZoom
    ? originalWidth < (pageWidth || Infinity)
      ? undefined
      : pageWidth
    : pageWidth;

  return (
    <Document {...docProps} className={documentClasses} onLoadSuccess={(e: PDFDocumentProxy) => {
      e.getMetadata().then((p) => {
        console.info(e.fingerprint, p.info);
      });
      setPageNum(e.numPages);
    }} error={() => (
      <div className={classes.loading}>
        Failed to load PDF: {errorMessage}
      </div>
    )} noData={() => (
      <div className={classes.loading}>
        Error: No PDF file specified.
      </div>
    )} loading={() => (
      <div className={classes.loading} />
    )} onLoadError={(e) => {
      console.error(e);
      setErrorMessage(e.message);
    }}>
      <Page width={calculatedWidth} pageNumber={curPage} className={classes.page} inputRef={(instance) => {
        if(instance && pageRef.current !== instance) {
          pageRef.current = instance;
          const newWidth = instance.clientWidth;
          if(newWidth && pageWidth !== newWidth) {
            setTimeout(() => {
              setPageWidth(newWidth);
            }, 0);
          }
        }
      }} onLoadSuccess={(page) => {
        setOriginalWidth(page.originalWidth);
      }} onLoadProgress={(data) => {
        setLoadingProgress(data.loaded / data.total);
      }} loading={() => (
        <div className={classes.pageLoading}>
          Loading page {curPage}... {(loadingProgress * 100).toFixed(0) + '%'}
        </div>
      )} noData={() => (
        <div className={classes.pageNoData}>
          Error: No page specified.
        </div>
      )} />
      <div className={classes.controls}>
        <button disabled={curPage <= 1} onClick={() => setCurPage(curPage - 1)} type="button">&lt;</button>
        <span>
          {curPage}
          {" "}
          of
          {" "}
          {pageNum}
        </span>
        <button disabled={curPage >= (pageNum as number)} onClick={() => setCurPage(curPage + 1)} type="button">&gt;</button>
      </div>
    </Document>
  )
}

export default PdfViewer;
