interface ImageProps extends React.ImgHTMLAttributes<HTMLImageElement> {
  alt: string
  allowLargeImage?: boolean
  children?: React.ReactNode
}

export function Imaged(props: ImageProps) {
  const classes = useImageStyles();
  const {className, children, alt, allowLargeImage, ...extProps} = props;
  return (
    <div className={classes.container}>
      <img className={[className, classes.img, allowLargeImage ? classes.largeImage : undefined].join(' ')} alt={alt} {...extProps} />
      { children }
    </div>
  )
}


