const usePageContentStyle = createUseStyles({
  outerContainer: {
    transition: '0.3s',
    minHeight: '30vh',
    fallback: {
      minHeight: '300px',
    },
    padding: 20,
  },
  pageContentWrapper: {
    maxWidth: getWidthLevelMax(2),
    margin: [[0, 'auto']],
  },
});

interface FrameworkPageProps extends React.HTMLAttributes<HTMLDivElement> {
  title: string
  className?: string
}

export function FrameworkPage(props: FrameworkPageProps) {
  const classes = usePageContentStyle();
  const {title, className, children, ...extProps} = props;
  return (
    <div className={[classes.outerContainer, className].join(' ')} {...extProps}>
      <div className={classes.pageContentWrapper}>
        <Helmet title={title} />
        {children}
      </div>
    </div>
  )
}

function App() {
  const classes = useStyles();
  const [pageWidth, setPageWidth] = useState(document.body.clientWidth);
  const navRef = useRef<HTMLElement>(null);
  const [stuck, setStuck] = useState<boolean>(false);

  useEffect(() => {
    const handleResize = function(e: UIEvent) {
      setPageWidth(document.body.clientWidth);
    }
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  });

  useEffect(() => {
    const handleReposition = function(...args: any) {
      if(navRef.current) {
        setStuck(navRef.current.getBoundingClientRect().top <= 1);
      }
    };
    handleReposition(); // initial
    const debonucedHandleReposition = Debounce(handleReposition, 50);
    window.addEventListener('resize', debonucedHandleReposition);
    window.addEventListener('scroll', debonucedHandleReposition);
    return () => {
      window.removeEventListener('resize', debonucedHandleReposition);
      window.removeEventListener('scroll', debonucedHandleReposition);
    }
  });

  const widthLevel = calcWidthLevel(pageWidth);

  // const TitleName = [
  //   'Undergrad',
  //   'Undergraduate',
  //   'Undergraduate',
  //   'Undergraduate Student',
  //   'Undergraduate Student',
  // ];
  
  const DepartmentName = [
    'Collage of Computer Science',
    'Collage of Computer Science | Zhejiang University',
    'Collage of Computer Science and Technology | Zhejiang University',
    'Collage of Computer Science and Technology | Zhejiang University',
    'Collage of Computer Science and Technology | Zhejiang University',
  ];

  const pageTitleKey = widthLevel < 1 ? 'shortTitle' : 'title';

  const navBarContent = (
    <ul>
      {
        Object.keys(ContentPages).filter(uri => !!ContentPages[uri].title).map((uri) => (
          <li key={uri}>
            <Link to={uri} className={classes.navButton}>
              { ContentPages[uri][pageTitleKey] }
            </Link>
          </li>
        ))
      }
    </ul>
  )

  return (
    <Route render={(params) => {
      const { location } = params;
      return (
        <WidthLevelContext.Provider value={widthLevel}>
          <div className={classes.framework}>
            <Helmet>{ prefetching.map(v => <link rel="prefetch" href={v} key={v} />) }</Helmet>
            <header className={classes.header}>
              <h1>Zihan Yan {/* <span className={classes.originalName}>严子涵</span> */}</h1>
              {/* <p>{TitleName[widthLevel]}</p> */}
              <p>{DepartmentName[widthLevel]}</p>
              <nav ref={navRef} className={[classes.navBar, stuck ? classes.stuck : undefined].join(' ')} children={navBarContent} />
              <nav className={[classes.navBar, classes.navBarFloating, stuck ? classes.stuck : undefined].join(' ')} children={navBarContent} />
            </header>
            <TransitionGroup component="main" className={classes.pageContainer}>
              <CSSTransition key={location.pathname} classNames={{ enter: classes.pageEnter, exit: classes.pageExit }} timeout={1}>
                <Switch location={location} key={location.pathname}>
                  { createDebugPageRoutes() }
                  { 
                    Object.keys(ContentPages).map((uri) => (
                      <Route exact key={uri} path={uri} component={ContentPages[uri].component} />
                    ))
                  }
                  <Route render={() => <Redirect to="/" />} />
                  {/* <Route component={RouterDebugPage} /> */}
                </Switch>
              </CSSTransition>
            </TransitionGroup>
          </div>
          <footer className={classes.footer}>
            <div>
              <h3>Contact Information</h3>
              <p><a href="mailto:zihanyan@zju.edu.cn">zihanyan@zju.edu.cn</a></p>
              {/* <h3>Find me on</h3>
              <p><a href="https://github.com/yzihan"><FontAwesomeIcon icon={['fab', 'github']} /></a></p> */}
            </div>
            <div style={{display: true ? undefined : 'none'}}>
              <p>&copy; 2020 Zihan</p>
              {/* <p>Built with <a href="https://reactjs.org">React</a></p> */}
              {/* <p>Footer animation from Manuel Pinto on <a href="https://codepen.io/P1N2O/pen/pyBNzX">CodePen</a></p> */}
            </div>
            <ul className={classes.footerBg} style={{clear: 'both'}}>
              {

              }
              <li />
              <li />
              <li />
              <li />
              <li />
              <li />
              <li />
              <li />
              <li />
              <li />
            </ul>
          </footer>
        </WidthLevelContext.Provider>
      );
    }} />
  )
}

export default App;
