import React from 'react';
import SyntaxHighlighter from 'react-syntax-highlighter';

export { FrameworkPage, Imaged } from './App';



const MinWidthLevel = 300;
const WidthLevels = [600, 800, 1000, 1200, Infinity];

export const calcWidthLevel = (width: number) => {
  return WidthLevels.findIndex(curLevelMaxWidth => width <= curLevelMaxWidth);
}

export const getWidthLevelMax = (level: number) => {
  return WidthLevels[level];
}

export const getWidthLevelMin = (level: number) => {
  return (level && WidthLevels[level - 1]) || MinWidthLevel;
}


export const WidthLevelContext = React.createContext<number>(0);



type SecondAndNextParameter<A1, T> = T extends (a1: A1, ...args: infer P) => any ? P : never;
  
interface JsonBlockProps extends React.HTMLAttributes<HTMLElement> {
  value: any
  varName?: string
  args?: SecondAndNextParameter<string, JSON["stringify"]>
}

export function JsonBlock(props: JsonBlockProps) {
  const { value, args, varName, ...extprops } = props;

  return (
    <SyntaxHighlighter language="javascript" showLineNumbers={true} {...extprops}>
      { (varName ? varName + " = " : "") + JSON.stringify(value, ...args || [null, 2]) }
    </SyntaxHighlighter>
  )
}


export function Debounce<Args extends any[]>(f: (...args: Args) => void, timeout: number = 100) {
  let debounce: NodeJS.Timeout | null = null;
  return (...args: Args) => {
    if(!debounce) {
      debounce = setTimeout(() => {
        f(...args);
        debounce = null;
      }, timeout);
    }
  }
}

// eslint-disable-next-line import/no-anonymous-default-export
export default {};